// @ts-nocheck
import React, { useCallback } from "react";


export interface IFirebaseContext {
  getRequest: (key: string) => Promise<IFetchResponseBase>;
  setRequest: (request) => Promise<IFetchResponseBase>;
  getRequests: () => Promise<IFetchResponseBase>;
  setAnswer: (answer, requestTimestamp) => Promise<IFetchResponseBase>;
}

export const FirebaseContext = React.createContext<IFirebaseContext>({
  getRequest: () => Promise.resolve({ isSuccessful: false }),
  setRequest: () => Promise.resolve({ isSuccessful: false }),
  getRequests: () => Promise.resolve({ isSuccessful: false }),
  setAnswer: () => Promise.resolve({ isSuccessful: false }),
});

const fetchBase =
  process.env.NODE_ENV === "development"
    ? ""
    : "https://network5-backend.herokuapp.com";

export const FirebaseProvider: React.FC = ({ children }) => {
  const getRequest = useCallback(
    async (timeStamp: string): Promise<IFetchResponseBase> => {
      const fetchRes = await fetch(fetchBase + `/users/request/${timeStamp}`, {
        method: "GET",
      });

      if (fetchRes.ok) {
        return await fetchRes.json();
      }
    },
    []
  );

  const setRequest = useCallback(
    async (request): Promise<IFetchResponseBase> => {
      const fetchRes = await fetch(fetchBase + `/users/request`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ request: request }),
      });

      if (fetchRes.ok) {
        return { isSuccessful: true };
      }

      return { isSuccessful: false, message: fetchRes.statusText };
    },
    []
  );

  const getRequests =
    useCallback(async (): Promise<IFetchResponseBase> => {
      const fetchRes = await fetch(fetchBase + `/users/requests`, {
        method: "GET",
      });

      if (fetchRes.ok) {
        return await fetchRes.json();
      }
    }, []);

  const setAnswer = useCallback(
    async (answer, requestTimestamp): Promise<IFetchResponseBase> => {
      const fetchRes = await fetch(fetchBase + `/users/answer`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ answer: answer, requestTimestamp: requestTimestamp }),
      });

      if (fetchRes.ok) {
        return { isSuccessful: true };
      }

      return { isSuccessful: false, message: fetchRes.statusText };
    },
    []
  );
  return (
    <FirebaseContext.Provider
      value={{
        getRequest,
        setRequest,
        getRequests,
        setAnswer,
      }}
    >
      {children}
    </FirebaseContext.Provider>
  );
};
